
.filterOverlay{
  -webkit-transition: all 0.5s linear;
  transition: all 0.5s linear;
}
.loadMore{
  height: 100px;
  line-height: 100px;
  text-align: center;
}
.sort-up{
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
}
.icon-arrow-short{
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
}



